import axios from 'axios';
import TYPES from '../types.js'

const state = {
    seller:{},
    sellerShow: false   ,
    flag : true
}
const getters = {
    seller(state){
        return state.seller
    },
    sellerShow(state){
        return state.sellerShow;
    },
    flag(state){
        return state.flag;
    }


}
const actions = {
        //获取seller数据
        getSeller({commit}){
            // console.log(3)
                axios.get('data.json').then((res)=>{
                    // console.log(res)
                    commit(TYPES.GET_SELLER,res.data.seller)
                })
        },
        showDetail({commit}){
            commit(TYPES.SHOW_DETAIL)
        },
        hideDetail({commit}){
            commit(TYPES.HIDE_DETAIL)
        },
        collection({commit}){
            commit(TYPES.COLLECTION)
        }
}
const mutations = {
    //获取seller数据
    [TYPES.GET_SELLER](state,data){
        state.seller = data;
    },
   [TYPES.SHOW_DETAIL](state){
        state.sellerShow = true;
   },
   [TYPES.HIDE_DETAIL](state){
    state.sellerShow = false;
    },
    [TYPES.COLLECTION](state){
        state.flag = !state.flag;
    }
}
export default {
    state,
    getters,
    mutations,
    actions
}


