<template>
  <div id="app">
    <v-header></v-header>
    <!-- 路由切换 -->
    <div class="tab">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评价</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <v-footer></v-footer>
    <router-view/>
  </div>
</template>

<script>

import header from './components/header/header.vue'
import footer from './components/footer/footer'
export default {
  name: 'app',
  components:{
     'v-header' : header,
     'v-footer':footer
  },
  mounted(){
      this.$store.dispatch('getSeller');
      this.$store.dispatch('getRatings');
      this.$store.dispatch('getGoods');

  }
}
</script>

<style lang="less" scoped>
#app {
      .tab{
          width: 100%;
          height: 0.8rem;
          font-size: 0.28rem;
          color: rgb(240,20,20);
          line-height: 0.8rem;
          display: flex;
          position: absolute;
          top: 2.68rem;
          background: rgb(255,255,255);
          border-bottom:1px solid rgba(7, 17, 27, 0.1);
          .tab-item{
              flex:1;
              text-align: center;
              a{
                display: block;
                &.active{
                    color: red;
                   
                }
              }
          }
      }
}
</style>



