<template>
    <div class="container">
        <div class="main">           
        </div>
        <div class="footer">
            <div class="left">
                <div class="shopCar">
                    <!-- <img @click="showMyShopCar" class="shopCarImg" src="/static/imgs/emptycar.png" alt=""> -->
                    <img @click="showMyShopCar" class="shopCarImg" src="/static/imgs/car.png" alt="">
                    <!-- 我的购物车组件 -->
                    <!-- 传值 -->
                    <shopCarDetail :shopCarList='selectFoods' :totalCount='totalCount'></shopCarDetail>
                </div>
                <!-- 总价 -->
                <div class="totalPrice">
                    <p class="tPrice" :class='{"heightlight" : totalCount > 0}'>¥ <span>{{totalPrice}}</span> </p> 
                </div>
                <!-- 配送费 -->
                <div class="deliveryPrice">
                    <p class="dPrice">另需配送费¥{{seller.deliveryPrice}}元</p>
                </div>
            </div>
            <div class="right">
                <div class="minPrice">
                    <!-- <p>¥ <span>{{seller.minPrice}}</span> 起送</p>  -->
                    <!-- 起送 -->
                    <p>{{payDesc}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
import shopCarDetail from '../shopCarDetail/shopCarDetail'
export default {
    computed:{
        ...mapGetters([
            'seller',
            'goods',
           'detailShopCar'
        ]),
        selectFoods(){
            let foods = [];
            // 遍历菜单
            this.goods.forEach( (good) => {
                // 遍历具体商品
                    good.foods.forEach( (food) =>{ 
                        if( food.count > 0){
                            foods.push(food);
                        }
                    })
            })
            return foods;
        },
        totalPrice(){
            let total = 0;
            this.selectFoods.forEach( food => {
                total += food.count*food.price;
            });
            return total;

        },
        totalCount(){
            let count= 0 ;
            this.selectFoods.forEach( (food)=>{
                count +=food.count
            });
            return count;
        },
        payDesc(){
				if( this.totalPrice === 0){
					return `￥${this.seller.minPrice}元起送`;
				}else if( this.totalPrice < this.seller.minPrice ){
					return `还差￥${this.seller.minPrice - this.totalPrice}元起送`
				}else{
					return "去结算";
				}
			}

    },
    components:{
        shopCarDetail
    },
    methods:{
        ...mapActions([
            'showMyShopCar'
        ])
    }
}
</script>
<style lang="less" scoped>
    .container{
    position: fixed;
    z-index:10000;
    width: 100%;
    height: 0.95rem;
    left:0;
    /* top:0;*/
    bottom: 0;
    z-index: 10;
    /* overflow: auto;*/
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -webkit-flex-direction: column; 
    color:#fff;
    }
    .main{
        flex: 1;
    }
    .footer{
        height: 0.94rem;
        background: #141d27;
        display: flex;
        .left{
           width: 75%;
           box-sizing: border-box;
           padding-left: 0.36rem;
           display: flex;
           .shopCar{
               width: 0.8rem;
               height: 0.8rem;
               border-radius: 0.8rem;
               background: rgba(255,255,255,0.2);
               border: 12px solid rgb(20,29,39);
               margin-top: -0.2rem;
               text-align: center;
            .shopCarImg{
                   width: 0.4rem;
                   margin-top:0.2rem;
               }
                              
           }
           .totalPrice{
               width: 1rem;
               border-right: 1px solid rgba(255,255,255,0.1);
               margin:0.24rem 0 0.24rem 0.12rem;
               height: 0.5rem;
               .tPrice{
                    font-size: 0.32rem;
                    color: rgba(255,255,255,0.4);
                    font-weight: bold;
                    line-height: 0.48rem;  
                    &.heightlight{
                        color: #fff;
                    }
               }
           }
           .deliveryPrice{            
               margin:0.24rem;
               .dPrice{
                    font-weight: 0.32rem;
                    color: rgba(255,255,255,0.4);                    
                    line-height: 0.48rem;
               }
           }
        }
        .right{
            width: 25%;
            background: rgba(255,255,255,0.2);
            .minPrice{
                font-size: 0.24rem;
                color: rgba(255,255,255,0.4);
                font-weight: bold;
                line-height: 0.48rem; 
                text-align: center;
                margin-top: 0.24rem;
            }
        }
    }
</style>




