<template>
    <div class="header"> 

        <div class="headbg">
            <img :src="seller.avatar" alt="">
        </div>
        <!-- 头部第一部分 -->
        <div class="headone">
            <img :src="seller.avatar" >
            <!-- 头部右侧文字 -->
            <div class="headtext">
                <div class="band">
                    <img  src="/static/imgs/brand.png" alt="">
                    <p class="sellername">{{seller.name}}</p>
                </div>
                    
                    <p class="sellerdes">{{seller.description}}／{{seller.deliveryTime}}分送达</p>
                <!-- 满减 -->
                <div class="sellersupports">
                    <img src="/static/imgs/decrease.png" alt="">
                    
                    <p v-if="seller.supports">{{seller.supports[0].description}}</p>
                </div>
                <div class="more" @click="showDetail()"><p v-if="seller.supports">{{seller.supports.length}}个<img src="../../../static/imgs/more.png" alt=""></p></div>
            </div>
        </div>
        <!-- 头部第二部分 -->
        <div class="headtwo">
             <img src="/static/imgs/bulletin.png" alt="">
             <p>{{seller.bulletin}}</p>
             <img class="moredes" src="/static/imgs/more.png" alt="">
        </div>
        <sellerDetail></sellerDetail>
    </div>
</template>
<script>
import sellerDetail from '../sellerDetail/sellerDetail.vue'
import { mapGetters, mapActions } from 'vuex'
export default {
    computed:{
        ...mapGetters([
            'seller'
        ])
    },
    components:{
        sellerDetail
    },
    methods:{
        ...mapActions([
            'showDetail'
        ])
        // showDetail(){
        //     this.$store.dispatch('showDetail')
        // }
    }
}
</script>
<style scoped lang="less">
    .header{
        width: 100%;
        height: 2.68rem;
        background:rgba(7,17,27,0.5);
        position: absolute;
        z-index: 1;
        box-sizing: border-box;
        overflow: hidden;
        .headbg{
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;            
            filter: blur(0.1rem);
            z-index: -10;
            img{
                width: 100%;
                height: 100%;
            }
        }
        /*头部第一部分 */
        .headone{
            height:2.12rem;
            box-sizing: border-box;
            padding: 0.48rem 0.24rem 0 0.48rem;
            img{
                width: 1.28rem;
                height: 1.28rem;
                border-radius: 0.04rem;
            }
            /*头部右侧文字*/
            .headtext{
                height: 1.28rem;
                padding: 0.04rem 0 0.04rem 0.32rem;
                display: inline-block;
                vertical-align: top;
                box-sizing: border-box;
                // position: relative;
                // left: 1.6rem;
                // top:-1.32rem;
                .band{
                    
                    height: 0.42rem;  
                     img{
                        width: 0.6rem;
                        height: 0.36rem;                    
                     }
                     .sellername{
                        font-size: 0.32rem;
                        color:white;
                        font-weight: bold;
                        line-height: 0.36rem;
                        display: inline-block;
                        vertical-align: top;
                        padding: 0.02rem 0 0 0.1rem;
                     }
                }             
                .sellerdes{
                    font-size: 0.2rem;
                    color:white;
                    font-weight: 200;
                    line-height: 0.4rem;
                    height: 0.44rem;
                }
                /*满减*/
                .sellersupports{
                    height: 0.42rem;
                  img{
                        width: 0.32rem;
                        height: 0.32rem;
                    }
                    p{
                        color: #fff;
                        font-size: 0.2rem;
                        display: inline-block;
                        vertical-align: top;
                        line-height: 0.24rem;
                        padding: 0.06rem 0 0 0.08rem;
                    }
                }
                .more{
                    width: 0.9rem;
                    height: 0.48rem;
                    background: rgba(0, 0, 0, 0.2);
                    text-align: center;
                    line-height: 0.48rem;
                    border-radius: 0.24rem;
                    color:#fff;     
                    position: absolute;
                    right: 0.24rem;
                    bottom: 0.9rem;
                    img{
                        width: 0.15rem;
                        height: 0.15rem;
                        margin-left: 0.05rem;
                    }           
                    
                }
                
            }
        }
        /*头部第2部分,头部公告部分*/
        .headtwo{
            width:100%;
            height: 0.56rem;
            background: rgba(7,17,27,0.2);
            padding: 0.16rem 0.24rem 0 0.24rem;
            z-index: 10;
            box-sizing: border-box;
            display: flex;
             img{
                width: 0.44rem;
                height: 0.24rem;
                
            }
              p{
                display: inline-block;
                font-size: 0.2rem;
                color:#fff;
                width: 95%;
                white-space:nowrap;
                overflow:hidden;               
                text-overflow:ellipsis; 
                margin:0.02rem 0 0 0.1rem;                
            }
            .moredes{
                width: 0.2rem;
                height: 0.2rem;
            }
                       
           
          
        }
         
        
        }
</style>




