<template>
  <div class="main" ref="main" id="main">
    <div>
    <!-- 第一部分start -->
      <div class="part1 bg">
        <!-- 商家名称 -->
          <div class="sellinfo1">
            <p>{{seller.name}}</p>
            <div class="count">
              <star :score="seller.score" ></star> 
              <p class="ratingcount">({{seller.ratingCount}})</p>
              <p class="sellcount">月售{{seller.sellCount}}单</p>             
            </div>                       
          </div>
           <!-- 收藏 -->
              <div class="collect" v-show="flag" @click="collection">
                  <div>
                    <img src="/static/imgs/uncollect.png" alt="">
                  </div>
                  <p class="collecttext">收藏</p>
              </div>
           <!-- 收藏 -->
          <div class="collect" v-show="!flag" @click="collection">
            <div>
              <img src="/static/imgs/collect.png" alt="">
            </div>
            <p class="collecttext">已收藏</p>
          </div>
          <!-- 起送价，配送费，配送时间 -->
          <div class="sellinfo2">
            <div class="minPrice">
              <p class="priceText1">起送价</p>
              <p class="priceText2">{{seller.minPrice}}<span>元</span></p>
            </div>
            <div class="deliveryPrice minPrice">
              <p class="priceText1">商家配送</p>
              <p class="priceText2">{{seller.deliveryPrice}}<span>元</span></p>
            </div>
            <div class="deliveryTime minPrice">
              <p class="priceText1">平均配送时间</p>
              <p class="priceText2">{{seller.deliveryTime}}<span>分钟</span></p>
            </div>
          </div>
      </div>
    <!-- 第一部分end -->
    <!-- 第二部分start -->
      <div class="part2 bg">
          <!-- 公告与活动 -->
          <div class="notice">
            <p class="noticeTitle">公告与活动</p>
            <p class="noticeContent">{{seller.bulletin}}</p>
          </div>
          <!-- 循环部分 -->
          <div class="supports" v-for="(v,k) in seller.supports" :key="k">
          <!-- <div class="supports"> -->
              <div>
                <!-- <img :src={{seller.supports | iconImg}}  alt=""> -->
                <img src="/static/imgs/discount.png" alt="">
              </div>
              <p class="supportsText">{{v.description}}</p>
              <!-- <p class="supportsText">qqqww</p> -->
          </div>
      </div>
      <!-- 第二部分end -->
      <!-- 第三部分start -->
      <div class="part3 bg">
        <!-- 商家实景 -->          
            <p class="sellerView">商家实景</p>
            <!-- 图片 -->
            <div class="pic-wrapper" ref='picwrap'>                    
              <ul class="pic-list" ref = 'piclist'>
                <li  class="pic-item" v-for="(v,k) in seller.pics" :key="k">
                  <img  :src="v" alt="">
                </li>
              </ul>
            </div>
          
      </div>
      <!-- 第三部分end -->
      <!-- 第四部分start -->
      <div class="part4 bg">
          <p class="sellTitle">商家信息</p>          
          
          <!-- 循环部分 -->
          <div class="infos" v-for="(v1,k1) in seller.infos" :key="k1">
          <!-- <div class="infos"> -->
             
              <p class="infosText">{{v1}}</p>
              <!-- <p class="infosText">qqqww</p> -->
          </div>
      </div>
      <!-- 第四部分end -->
      </div>
  </div>
</template>
<script>
import star from '../star/star'
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
export default {
    computed:{
      ...mapGetters([
          'seller',
          'flag'
      ])
    },
    components:{
      star
    },
		watch:{
        seller(){
          this._initPicture();
        }
		},
		mounted(){
			this.$nextTick( () => {
        if( !this.sellerSCroll ){
          this.sellerSCroll = new BScroll( document.getElementById('main') ,{
            click : true
          });
        }else{
          this.sellerSCroll.refresh();
        }
      });
      this._initPicture();
    },
    methods:{
        collection(){
          this.$store.dispatch('collection')
        },
        _initPicture(){
				if( this.seller.pics){
					let pw = 120;
					let m = 12;
					let w = ( pw + m ) * this.seller.pics.length - m;
					this.$refs.piclist.style.width = w + 'px';

					if( !this.picBScroll ){
						this.picBScroll = new BScroll( this.$refs.picwrap ,{
							scrollX : true ,
							eventPassthrough : 'vertical'
						})
					}else{
						this.picBScroll.refresh();
					}
				}
      }
		},
    filters:{
        iconImg : function(value){
        let path = '';       
        for (var i = 0 ; i < value.length; i++){
            if(value[i].type==0){
                path = '/static/imgs/decrease.png';
            }else if(value[i].type==1){
                path = '/static/imgs/discount.png';
            }else if(value[i].type==2){
                path = '/static/imgs/discount.png';
            }else if(value[i].type==3){
                path = '/static/imgs/discount.png';
            }else 
               path = '/static/imgs/guarantee.png';
                 
        return path; 
        }
     }
  }
	}
</script>
<style lang="less" scoped>
    .main{
      width: 100%;
      // height: 100%;
      background: #F3F5F7;
      position: absolute;
		  top: 3.6rem;
		  bottom: 1rem;
      z-index: -55;
      overflow: hidden;       
      .bg{
        background: rgb(255,255,255);
        margin-bottom: 0.36rem;
        box-sizing: border-box;           
      }
      
     /*-------part1---start-------*/
     .part1{
      /* display: none;*/
        box-sizing: border-box;
        padding: 0.36rem;
        border-bottom: 2px solid rgba(7,17,27,0.1);
        .sellinfo1{
          box-sizing: border-box;
          border-bottom: 1px solid rgba(7,17,27,0.1);
          p{
            font-size: 0.28rem;
            color: rgb(7,17,27);
            line-height: 0.2rem;
            margin-bottom:0.16rem; 
          } 
          .count{
            // margin: 0.16rem 0 0.36rem 0;
            display: flex;
            margin-bottom: 0.18rem;
            box-sizing: border-box;
            .ratingcount{
              font-size: 0.2rem;
              color: rgb(77,85,93);
              line-height: 0.36rem;
              margin-left: 0.16rem;
            }
            .sellcount{
              font-size: 0.2rem;
              color: rgb(77,85,93);
              line-height: 0.36rem;
              margin-left: 0.24rem;
            }
            img{
              width: 0.32rem;
              height: 0.32rem;
            }                  
          }     
        }
        /*收藏*/
        .collect{
          float:right;
          margin-top:-1.2rem;
          text-align: center;
          img{
            width: 0.5rem;
            height: 0.5rem;
          }
          .collecttext{
              font-size: 0.2rem;
              color: rgb(77,85,93);
              line-height: 0.2rem;
              margin-top: 0.08rem;
            }
        }

        /*sellinfo2---start----*/
        .sellinfo2{
          display: flex;
          text-align: center;
          justify-content: space-around;
          padding-top: 0.36rem;
          box-sizing: border-box;

          div{
            // border: 1px solid red;
            width: 32%;
            
          }
          .minPrice{
            .priceText1{
              font-size: 0.2rem;
              color: rgb(147,153,159);
              line-height: 0.2rem;
              
            }
             .priceText2{
              font-size: 0.48rem;
              color: rgb(7,17,27);
              line-height: 0.48rem;
              margin-top: 0.08rem;
              span{
                font-size: 0.18rem;
              }
            }

          }
          .deliveryPrice{
            border-left:1px solid rgba(7,17,27,0.1)
          }
          .deliveryTime{
            border-left:1px solid rgba(7,17,27,0.1)
          }
        }
        /*sellinfo2---end----*/        
      }
      
     /*-------part1---end-------*/
     /*-------part2---start-------*/
     .part2{
       box-sizing: border-box;
       padding: 0.36rem 0.36rem 0 0.36rem;
       border-top: 2px solid rgba(7,17,27,0.1);
       border-bottom: 2px solid rgba(7,17,27,0.1);
       .notice{
         .noticeTitle{
           font-size: 0.28rem;
           color: rgb(7,17,27);
           line-height: 0.28rem;
           margin-bottom: 0.16rem;
         }
         .noticeContent{
            margin-left: 0.24rem;
            font-size: 0.24rem;
            font-weight: 200;
            color: rgb(240,20,20);
            line-height: 0.48rem;
         }
       }
       .supports{
         display: flex;
         border-top:1px solid rgba(7,17,27,0.1);
         box-sizing: border-box;
         padding: 0.32rem 0.32rem 0.32rem 0.14rem;
       }
       .supportsText{
         font-size: 0.24rem;
         font-weight: 200;
         color: rgb(7,17,27);
         line-height: 0.32rem;
         margin-top: 0.16rem;
       }
     }
     /*-------patr2---end-------*/
     /*-------part3---start-------*/
     .part3{
       border-top:2px solid rgba(7,17,27,0.1);
       border-bottom:2px solid rgba(7,17,27,0.1);
       box-sizing: border-box;
       padding: 0.36rem;
       .sellerView{
          font-size: 0.28rem;
           color: rgb(7,17,27);
           line-height: 0.28rem;
           margin-bottom: 0.16rem;          
       }
       .pic-wrapper{
          width: 6.85rem;
          white-space: nowrap;
          overflow: hidden;
          .pic-item{
            display: inline-block;
            margin-right:0.12rem;
            &:last-child{
              margin:0;             
            }
             img{
                width: 2.4rem;
                height: 1.8rem;
              }
          }
       }
     }
     /*-------part3---end-------*/
     /*-------part4---start-------*/
     .part4{
       box-sizing: border-box;
       padding: 0.36rem 0.36rem 0 0.36rem;
       border-top: 2px solid rgba(7,17,27,0.1);
       .sellTitle{
          font-size: 0.28rem;
           color: rgb(7,17,27);
           line-height: 0.28rem;
           margin-bottom: 0.16rem;
           } 
       .infos{
            border-top:1px solid rgba(7,17,27,0.1);
            box-sizing: border-box;
            padding: 0.32rem 0.32rem 0.32rem 0.14rem;
          .infosText{
              font-size:0.24rem;
              font-weight: 200;
              color: rgb(7,17,27);
              line-height: 0.32rem;
            }
          }       
     }
     
     /*-------part4---end-------*/
    }
</style>


