<template>
	<transition enter-active-class="animated fadeInRight" leave-active-class="animated fadeOutRight">
		<div class="mainPage" v-show="detailShow" id="mainPage">
		<div>
			<div class="back" @click="hideFoodDetail">
				<div class="backImg"><img src="/static/imgs/back.png" alt=""></div>
			</div>
			<div class="topImg">
				<img :src="food.image" alt="">
			</div>
			<!-- 菜名 -->
			<div class="foodName">
				<div class="name">{{food.name}}</div>
				<div class="sellCount">月售{{food.sellCount}}份 <span>好评率{{food.rating}}％</span> </div>
				<div class="price"> 
					<span class="newPrice">¥{{food.price}}</span> 
					<span class="oldPrice" v-show="food.oldPrice">¥{{food.oldPrice}}</span> 
				</div>
				<div class="shopCar" v-show="!food.count" @click="add1">加入购物车</div>
				<!-- 购物车增加减少组件 -->
				<addAndDel class="carControl" :food = 'food' v-show="food.count"></addAndDel>
			</div>
			<!-- 商品介绍 -->
			<div class="introduce">
				<div class="introduceTitle">商品介绍</div>
				<div class="introduceInfo">{{food.info}}</div>
			</div>
			<!-- 商品评价 -->
			<div class="ratings">
				<div class="ratingsTitle"> 商品评价</div>
				<!-- 评论组件 -->
				 <foodRatingSelect></foodRatingSelect>
				<!-- 具体评价  -->
				<div class="commentpart2" v-for="(rating,k) in foodSelectRatings" :key='k'>
				<div class="br">
					<div class="commentpart2Left">
						<!-- 评价时间 -->
						<p class="commTime">{{rating.rateTime | moment}}</p>
						<!-- 评价内容 -->
						<p class="commCont" v-show="rating.text">
							<img class="agreeImg" src="/static/imgs/disagree.png" alt=""> 
							{{rating.text}}
						</p>
					</div>
					<div class="commentpart2Right">
						<!-- 用户名称，头像 -->
						<p class="commUser">{{rating.username}}<img :src="rating.avatar" alt=""> </p>
					</div>
				</div>
				</div>
				<div class="bottom" style="height:0.8rem"></div>
			</div>
		</div>
		</div>
	</transition>
</template>

<script>
	import BScroll from 'better-scroll'
	import { mapGetters,mapActions } from 'vuex'
	import moment from 'moment'
	import animate from 'animate.css'
	import addAndDel from '../addAndDel/addAndDel.vue'
	import foodRatingSelect from '../foodRatingSelect/foodRatingSelect'
	export default {

		computed : {
			...mapGetters([
				'detailShow',
				'food',
				'ratings',
				'foodSelectRatings'
			])
		},
		components : {
			addAndDel,
			foodRatingSelect
		},
		methods:{
			...mapActions([
				'hideFoodDetail'
			]),
			add1(){
				this.food.count = 1;
			}
		},
		filters:{
			moment : function(value, formatString){
			formatString = formatString || 'YYYY-MM-DD HH:mm';
			return moment(value).format(formatString);
			}
		},
		mounted(){
			this.$nextTick( () => {
				if( !this.foodDetailSCroll ){
				this.foodDetailSCroll = new BScroll( document.getElementById('mainPage') ,{
					
				});
				}else{
				this.foodDetailSCroll.refresh();
				}
			})
		}
	}

</script>

<style lang='less' scoped>
	.mainPage{
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 100;
		background: #f3f5f7;
		.back{
			width: 0.6rem;
			height: 0.6rem;
			background: rgba(0,0,0,0.5);
			position: absolute;
			top:0.2rem;
			left:0.2rem;
			border-radius: 0.5rem;
			text-align: center;
			.backImg{
				margin-top: 0.15rem;
				margin-left: -0.06rem;
				img{
					width: 0.3rem;
				}
			}
			
		}
		.topImg{
			img{
				width: 100%;
				height: 7.5rem;
			}
		}
		.foodName{
			background: #FFF;
			box-sizing: border-box;
			padding: 0.36rem;
			margin-bottom: 0.32rem;
			border-bottom:2px solid rgba(7, 17, 27, 0.1);
			.name{
				font-size:0.28rem;
				font-weight: 700;
				color: rgb(7,17,27);
				line-height: 0.28rem;
				margin-bottom: 0.16rem;
			}
			.sellCount{
				font-size:0 .2rem;
				color: rgb(147,153,159);
				line-height: 0.2rem;
				margin-bottom: 0.36rem;
			}
			.price{
				.newPrice{
					font-size: 0.28rem;
					font-weight: 700;
					color: rgb(240,20,20);
					line-height: 0.48rem;
				}
				.oldPrice{
					font-size: 0.20rem;
					font-weight: 700;
					color: rgb(147,153,159);
					line-height: 0.48rem;
					margin-left: 0.1rem;
					text-decoration:line-through;
				}
			}
			.shopCar{
				float: right;
				width: 1.48rem;
				height: 0.48rem;
				background: rgb(0,160,220);
				border-radius: 0.24rem;
				text-align: center;
				color: rgb(255,255,255);
				font-size: 0.2rem;
				line-height: 0.48rem;
				position: relative;
				top:-0.5rem;
		
			}
			.carControl{
				float: right;
				width: 1.48rem;
				height: 0.48rem;
				position: relative;
				top:-0.5rem;
			}
		}
		.introduce{
			background: #FFF;
			box-sizing: border-box;
			padding: 0.36rem;
			margin-bottom: 0.32rem;
			border-top:2px solid rgba(7, 17, 27, 0.1);
			border-bottom:2px solid rgba(7, 17, 27, 0.1);
			.introduceTitle{
				font-size: 0.28rem;
    			color: #07111b;
    			line-height: 0.28rem;
				margin-bottom: 0.12rem;
			}
			.introduceInfo{
				padding-left: 0.16rem;
				font-size: 0.24rem;
    			color: rgb(77,85,93);
    			line-height: 0.48rem;
			}
			
		}
		.ratings{
			background: #FFF;
			border-top:2px solid rgba(7, 17, 27, 0.1);
			.ratingsTitle{
				box-sizing: border-box;
				padding:0.36rem 0.36rem 0 0.36rem;
				font-size: 0.28rem;
    			color: #07111b;
    			line-height: 0.28rem;
			}

			.commentpart2{
				box-sizing: border-box;
				padding: 0 0.36rem;
				.br{
					display: flex;
					border-bottom: 1px solid rgba(7, 17, 27, 0.1);
					padding: 0.32rem 0;
					box-sizing: border-box;
					.commentpart2Left{
					width: 75%;
					.commTime{
						font-size: 0.2rem;
						color: rgb(147,153,159);
						line-height: 0.24rem;
						margin-bottom: 0.12rem;
					}
					.commCont{
						font-size: 0.24rem;
						color: rgb(7,17,27);
						line-height: 0.32rem;
						.agreeImg{
							width: 0.32rem;
							margin-top: 0.1rem;
						}
					}
				}
				/*用户名称，头像*/
				.commentpart2Right{
					width: 25%;
					text-align: right;
					.commUser{
						font-size: 0.2rem;
						color: rgb(147,153,159);
						line-height: 0.24rem;
						img{
							width: 0.3rem;
							border-radius: 0.3rem;
							position: relative;
							top:0.08rem;
							margin-left: 0.12rem;
						}
					}
				}
				}
				
			}
		}
	}
</style>


