<template>
  <div v-show="detailShopCar" class="shopCarBg">
      <div class="car">
          <div class="carTop" @click="hideShopCar">
          </div>
          <div class="carFooter">
              <div class="part1">
                  <div class="part1Left">购物车</div>
                  <div class="part1Right" ><span @click="clear">清空</span></div>
              </div>
              <div class="part2" ref='cc'>
                  <div>
                  <!-- 遍历购物车的商品 -->
                        <div class="br" v-for="(v,k) in shopCarList" :key='k'>
                            <div class="part2Left">{{v.name}}</div>
                            <div class="part2Middle"><span>¥</span> {{v.price}}</div>
                            <div class="part2Right">
                                <!-- 购物车增加减少组件 -->
                                <!-- 传值 -->
                            <addAndDel :food = 'v'></addAndDel>
                            </div>                            
                        </div> 
                    </div>  
                  </div>               
              </div>              
          </div>
      </div>
  </div>
</template>
<script>
import addAndDel from '../addAndDel/addAndDel'
import {mapActions,mapGetters} from 'vuex'
import BScroll from 'better-scroll'

export default {
    props:[
        'shopCarList'
    ],
    computed:{
        ...mapGetters([
            'detailShopCar'
        ])
    },
    methods:{
        ...mapActions([
            'hideShopCar'            
        ]),
        clear(){
            this.shopCarList.forEach( food =>{
                food.count = 0;
            })
        }
    },
    components:{
        addAndDel
    },
    mounted(){
			this.$nextTick( () => {
				if( !this.shopCarSCroll ){
					this.shopCarSCroll = new BScroll( this.$refs.cc ,{
                        click : true
                    });
                    
				}else{
					this.shopCarSCroll.refresh();
                }
                
			});			
		}
}
</script>

<style lang="less" scoped>
    .shopCarBg{
        position: fixed;
		top: 0;
		right: 0;
		bottom: 0.93rem;
		left: 0;
        width: 100%;
        background: rgba(7,17,27,0.6);
        overflow: hidden;
        // filter: blur(0.1rem);
        .car{
            height: 100%;
            background: rgba(7,17,27,0.6);
            position: relative;
            .carTop{
            height: 10rem;
            }       
            .carFooter{
                max-height: 6.11rem;
                background: rgb(255,255,255);
                overflow: hidden;
                color: #000;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                text-align: left;
                .part1{
                    height: 0.8rem;
                    display: flex;
                    box-sizing: border-box;
                    padding: 0 0.36rem;                    
                    background: #f3f5f7;
                    border-bottom: 2px solid rgba(7,17,27,0.1);
                    .part1Left{
                    font-size: 0.28rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.8rem;
                    width: 50%;
                    }
                    .part1Right{
                    font-size: 0.28rem;
                    font-weight: 200;
                    color:rgb(0,160,220);
                    line-height: 0.8rem;
                    position: relative;
                    width: 50%;
                    text-align: right;
                    }
                }
                .part2{
                    max-height: 4.3rem;
                    box-sizing: border-box;
                    padding:0  0.36rem 0.24rem 0.36rem; 
                    overflow: hidden;
                    .br{
                        border-bottom: 1px solid rgba(7,17,27,0.1);
                        display: flex;
                        padding: 0.24rem 0;
                        box-sizing: border-box;
                        .part2Left{
                        width: 4rem;
                        font-size: 0.28rem;
                        color: rgb(7,17,27);
                        line-height: 0.48rem;
                    }
                    .part2Middle{
                        font-size: 0.48rem;
                        color: rgb(240,20,20);
                        line-height: 0.48rem;
                        width: 1.2rem;
                        span{
                            font-size: 0.36rem;
                        }
                    }
                   .part2Right{
                        // border: 1px solid red;
                   }    
                    }          
                }
            }
        }
    }
</style>



