<template>
  <div>
        <div class="ratingSelect">
            <div class="statistics">
                <div class="stat_all stat" :class="{'active' : selectType ==2 }" @click="select(2)">{{types[2]}}<span v-if='ratings'>{{ratings.length}}</span></div>
                <div class="stat_satisfied stat" :class="{'active' : selectType ==0 }" @click="select(0)">{{types[0]}}<span>{{ratings | satisfied}}</span></div>
                <div class="stat_unsatisfied stat" :class="{'active' : selectType ==1 }" @click="select(1)">{{types[1]}}<span>{{ratings | unsatisfied}}</span> </div>
            </div>
                  <!-- 只看有内容的评价 -->
            <div class="chose_comm" @click='toggle'>
                <img  src="/static/imgs/chose.png" alt="" v-show='!onlyContent'>
                <img  src="/static/imgs/chosen.png" alt="" v-show='onlyContent'>
                <p  >只看有内容的评价</p>              
            </div>                  
        </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex'
export default {
    computed:{
        ...mapGetters([
            'ratings',
            'selectRatings',
            'types',
            'selectType',
            'onlyContent'
        ])
    },
    methods:{
        select(type){
            this.$store.dispatch('select',{type})
        },
        toggle(){
            this.$store.dispatch('toggle');
        }
    },
    mounted(){
        this.$nextTick( () => {
           setTimeout( () => {
                this.$store.dispatch('changTypes',['满意','不满意','全部'])
           })
        }) 
        
    },
    filters:{
        satisfied : function(value){
        let x = 0;       
        for (var i = 0 ; i < value.length; i++){
            if(value[i].rateType ==0){
            x++;
            }
        }
        return x; 
        },
        unsatisfied : function(value){
        let x = 0;       
        for (var i = 0 ; i < value.length; i++){
            if(value[i].rateType ==1){
            x++;
            }
        }
        return x; 
        }
    }
}
</script>
<style lang="less" scoped>
    .ratingSelect{   
      box-sizing: border-box;
      padding: 0.36rem;
      .statistics{
        display: flex;
        .stat{
          font-size: 0.28rem;  
          height:0.65rem;
          border-radius: 0.02rem;
          text-align: center;
          padding: 0.2rem 0.36rem 0 0.36rem;    
          box-sizing: border-box; 
          color: #5c636a;
          span{
            margin-left:0.1rem;
            font-size: 0.2rem; 
          }          
        }
        .stat_all{
          background: #00a0dc;  
          &.active{
              color: #fff;
          }          
        }
        .stat_satisfied{
          background: #ccecf8;          
          margin: 0 0.16rem 0 0.16rem;
          &.active{
          color: #fff;
          }
        }
        .stat_unsatisfied{
          background: #e9ebec;   
          &.active{
          color: #fff;
          }       
        }
      }
      /*只看有内容的评价start*/
      .chose_comm{
        border-top: 1px solid rgba(7, 17, 27, 0.1);
        margin-top:0.36rem;
        box-sizing: border-box;
        padding-top: 0.4rem;       
            img{
              width: 0.48rem;
              height: 0.48rem;
            }
            p{
              display: inline-block;
              font-size: 0.2rem;
              font-weight: 200;
              color:rgb(147,153,159);
              line-heigth:0.24rem;
              vertical-align: top;
              margin: 0.15rem 0 0 0.1rem;              
            }
      }
       /*只看有内容的评价end*/
    }
</style>



