<template>
  <div>
        <div class="ratingSelect">
            <div class="statistics">
                <div class="stat_all stat" :class="{'active' : selectType1 ==2 }" @click="foodSelect(2)">{{types1[2]}}<span v-if='food.ratings'>{{food.ratings.length}}</span></div>
                <div class="stat_satisfied stat" :class="{'active' : selectType1 ==0 }" @click="foodSelect(0)">{{types1[0]}}<span>{{positiveCount1}}</span></div>
                <div class="stat_unsatisfied stat" :class="{'active' : selectType1 ==1 }" @click="foodSelect(1)">{{types1[1]}}<span>{{negativeCount1}}</span> </div>
            </div>
                  <!-- 只看有内容的评价 -->
            <div class="chose_comm"  @click='toggle1'>
                <img src="/static/imgs/chose.png" alt="" v-show='!onlyContent1'>
                <img src="/static/imgs/chosen.png" alt="" v-show='onlyContent1'>
                <p>只看有内容的评价</p>              
            </div>                  
        </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex'
export default {
    computed:{
        ...mapGetters([
            'ratings',
            'food',
            'selectRatings',
            'types1',
            'selectType1',
            'onlyContent1'
        ]),
			positiveCount1(){
                if(this.food.ratings){
                    return this.food.ratings.filter( rating => {
					return rating.rateType === 0;
				    }).length;
                }				
			},
			negativeCount1(){
                if(this.food.ratings){
                    return this.food.ratings.filter( rating => {
					return rating.rateType === 1;
				    }).length;
                }
				
			}
    },
    methods:{
        foodSelect(type){
            this.$store.dispatch('foodSelect',{type})
        },
        toggle1(){
            this.$store.dispatch('toggle1');
        }
    },
    mounted(){
        this.$nextTick( () => {
           setTimeout( () => {
                this.$store.dispatch('chang1Types',['推荐','吐槽','全部'])
           })
        }) 
        
    }
}
</script>
<style lang="less" scoped>
    .ratingSelect{   
      box-sizing: border-box;
      padding: 0.36rem;
      .statistics{
        display: flex;
        .stat{
          font-size: 0.28rem;  
          height:0.65rem;
          border-radius: 0.02rem;
          text-align: center;
          padding: 0.2rem 0.36rem 0 0.36rem;    
          box-sizing: border-box; 
          color: #5c636a;
          span{
            margin-left:0.1rem;
            font-size: 0.2rem; 
          }          
        }
        .stat_all{
          background: #00a0dc;  
          &.active{
              color: #fff;
          }          
        }
        .stat_satisfied{
          background: #ccecf8;          
          margin: 0 0.16rem 0 0.16rem;
          &.active{
          color: #fff;
          }
        }
        .stat_unsatisfied{
          background: #e9ebec;   
          &.active{
          color: #fff;
          }       
        }
      }
      /*只看有内容的评价start*/
      .chose_comm{
        border-top: 1px solid rgba(7, 17, 27, 0.1);
        margin-top:0.36rem;
        box-sizing: border-box;
        padding-top: 0.4rem;       
            img{
              width: 0.48rem;
              height: 0.48rem;
            }
            p{
              display: inline-block;
              font-size: 0.2rem;
              font-weight: 200;
              color:rgb(147,153,159);
              line-heigth:0.24rem;
              vertical-align: top;
              margin: 0.15rem 0 0 0.1rem;              
            }
      }
       /*只看有内容的评价end*/
    }
</style>



