<template>
    <div class="imgIcon">
        <div class="left">
            <div class="delIcon" @click="del" v-show="food.count>0">
            <img  src="/static/imgs/del.png" alt="">
            </div>
            <p class="iconText" v-show="food.count>0">{{food.count}}</p>
        </div>
        
        <div class="addIcon" @click="add">
            <img  src="/static/imgs/add.png" alt="">
        </div>            
    </div>

</template>
<script>
import Vue from 'vue'
import {mapGetters} from 'vuex'
export default {
    computed:{
        ...mapGetters([
            
        ])
    },
        props:[
            'food'
        ],
        methods:{
            add(){
                if(!this.food.count){
                    Vue.set(this.food,'count',1);                    
                }else{
                    this.food.count ++;
                }
            },
            del(){
                if( this.food.count ){
					this.food.count --;
				}
            }
        }
}
</script>

<style lang="less" scoped>
    .imgIcon{
        width: 1.8rem;
        display: flex;
        .left{
            width: 1.1rem;
            display: flex;
            text-align: center;
            .delIcon{
                width: 0.6rem;
                img{
                    width: 0.4rem;
                    height: 0.4rem;
                }
            }
            .iconText{
                width: 0.4rem;
                margin-top: 0.1rem;
            }
        }

        .addIcon{
            width: 0.3rem;
            img{
                width: 0.4rem;
                height: 0.4rem;
            }
        }
    }
</style>



