<template>
    <!-- 内容部分 -->
  <div class="main" id="main" rel="main"> 
     <!-- 左侧导航菜单 -->     
      <div class="tab" id="tab">
        <div>
          <div @click="selectTab(i)"   :class='{"current" : currentIndex == i}'  class="memu" v-for="(v,i) in goods" :key='i' >
            <div class="memuName">
              {{v.name}}
            </div>            
          </div> 
        </div> 
      </div>
      <!-- 右侧商品信息  -->
      <div class="content"  rel="content" id="content">                      
            <!-- 图片和菜品  -->
          <div>
            <div class="contentImgAndText" v-for="(v1,k1) in goods" :key='k1'>
                <div class="contentTitle">{{v1.name}}</div>
                <div v-for="(v2,k2) in v1.foods" :key='k2' class="content01">
                  <div class="br">
                   <!-- 图片 -->
                    <div  @click="showFoodDetail(v2)">
                      <img class="contentImg" :src="v2.icon" alt="">
                    </div>
                  <!-- 菜名，销量，价钱 -->
                    <div class="content02">
                        <p class="dishesName">{{v2.name}}</p>
                        <p class="dishesDescri">{{v2.description}}</p>
                        <p class="dishesDescri">月售{{v2.sellCount}}份 <span class="dishesDescri"> 好评率{{v2.rating}}%</span></p>
                        <div class="price">
                          <p class="dishesPrice">
                            ¥{{v2.price}} <span class="oldPrice" v-show="v2.oldPrice">¥{{v2.oldPrice}}</span>                     
                          </p>
                          <!-- 购物车组件 -->
                          <addAndDel class="img01" :food = 'v2' ></addAndDel>
                          <!-- <div class="imgIcon">
                            
                            <img class="add" src="/static/imgs/add.png" alt="">
                          </div> -->
                        </div>
                    </div>
                  </div>
                </div> 
            </div>
      </div>
      <foodDetail></foodDetail>  
      </div>  
  </div>
</template>
<script>
import foodDetail from '../foodDetail/foodDetail'
import {mapGetters,mapActions} from 'vuex'
import BScroll from 'better-scroll'
import addAndDel from '../addAndDel/addAndDel'
export default {
  computed:{
    ...mapGetters([
      'goods',
      'seller'
    ]),
    currentIndex(){
				for( let i = 0 ; i < this.heights.length ; i++){
					let height1	= this.heights[i];
					let height2	= this.heights[i+1];
					if( this.scrollY >= height1 && this.scrollY < height2){
						return i;
					}
				}
				return 0;
			}
  },
  data(){
    return {
      heights :[],
      scrollY:""
    }
  },
  components:{
    foodDetail,
    addAndDel
  },
  methods:{
      showFoodDetail( f ){
				this.$store.dispatch('showFoodDetail',f);
      },
      
      initBScroll(){
        // 左侧滚动
        if( !this.tabSCroll ){
          this.tabSCroll = new BScroll( document.getElementById('tab') ,{
            click : true
          });
        }else{
          this.tabSCroll.refresh();
        }
        // 右侧滚动
        if( !this.contentSCroll ){
          this.contentSCroll = new BScroll( document.getElementById('content') ,{
            click : true,
            probeType : 3
          });
        }else{
          this.contentSCroll.refresh();
        };        
        this.contentSCroll.on('scroll', (pos) => {
					// console.log(pos);
					this.scrollY = Math.abs( Math.round( pos.y))
					// console.log(this.scrollY);
				}) 
      },
      calcHeight(){
				let foodList = document.getElementsByClassName('contentImgAndText');
				let height = 0 ;
				this.heights.push( height );
				for(let i = 0 ; i < foodList.length ; i++){
					//获取每一个li（区块）的高度
					height += foodList[i].clientHeight;
					this.heights.push( height );
        }
        // console.log(this.heights)
      },
      // 左侧点击事件
			selectTab(i){
        // console.log(i);
        // console.log(1)
				let foodList = document.getElementsByClassName('contentImgAndText');
				this.contentSCroll.scrollToElement(foodList[i],300);
      }
     
  },
    mounted(){
      this.$nextTick( () => {
        setTimeout( () => {
          this.initBScroll();
          this.calcHeight();
        })
      })
    }
	}
</script>
<style lang="less" scoped>
 .main{
   width: 100%;
   display:flex;
   background: #F3F5F7;
   position: absolute;
	 top: 3.5rem;
	 bottom: 1rem;
   overflow: hidden;  
  /*左侧导航菜单--start----*/
   .tab{
     width: 1.8rem;
     box-sizing: border-box;
     /*热销榜*/
     .memu{
       height: 1.08rem;
       width: 1.8rem;
       padding: 0 0.24rem;
       box-sizing: border-box;
       display: table;
       &.current{
         background:rgb(255,255,255);
       }
       .memuName{
         box-sizing: border-box;
         font-size: 0.24rem;
         font-weight: 200;
         line-height: 0.28rem;
         display:table-cell; 
         vertical-align:middle;
         border-bottom: 1px solid rgba(7,17,27,0.1);   
        }
      }
   }
  /*左侧导航菜单--end----*/
  /* 右侧具体内容*/
   .content{
      width: 100%;
      
      .contentImgAndText{
       .contentTitle{
        font-size: 0.24rem;
        color: rgb(147,153,159);
        line-height: 0.52rem;
        height: 0.52rem;
        padding-left: 0.28rem;
        border-left:2px solid #d9dde1;
      }
      .content01{
        background: rgb(255,255,255);
        padding: 0.36rem 0.36rem 0 0.36rem;
        box-sizing: border-box;
        .br{
          display: flex;
          border-bottom: 1px solid rgba(7,17,27,0.1);  
          padding: 0 0 0.2rem 0;        
        .contentImg{
            width: 1.12rem;
            height: 1.12rem;
            border-radius: 2px;
          }
        .content02{
            margin:0.04rem 0  0 0.2rem;
        .dishesName{
            font-size: 0.28rem;
            color: rgb(7,17,27);
            line-height: 0.28rem;
            margin-bottom: 0.1rem;
          }
        .dishesDescri{
            font-size: 0.2rem;
            color: rgb(147,153,159);
            line-height: 0.28rem;
            margin-bottom: 0.08rem;
        }
        /* 价钱*/
        .price{
          display: flex;
          .dishesPrice{
            font-size: 0.28rem;
            color: rgb(240,20,20);
            font-weight: 700;
            line-height: 0.48rem;
            width: 2.3rem;
            .oldPrice{
              font-size: 0.2rem;
              color: rgb(147,153,159);
              font-weight: 700;
              line-height: 0.48rem;
              text-decoration:line-through;
            }
          }
          
          /* 图标
          .imgIcon{
              margin-left:1.6rem; 
            .add{
              margin-left: 0.3rem
            }
            img{
              width: 0.4rem;
            }
          }*/
        }
      }
    }
  }      
  }   
 }
   /*content ---end---*/

 }
</style>




