<template>
    <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
        <div v-show="sellerShow" class="sellerDetail">
            <div class="bg">
                <div class="main">
                    <!-- 第一部分 -->
                    <div class="part1">
                        <p class="sellername">{{seller.name}}</p>
                        <!-- 星星 -->
                        <div class="star">
                            <img src="/static/imgs/star_on.png" alt="">
                        </div>
                    </div>
                    <!-- 第二部分 优惠信息-->
                    <div class="part2">
                        <p class="line1"></p>
                        <p class="discout">优惠信息</p>                         
                        <p class="line2"></p>
                        <div class="discontent">
                            <div v-for="(v,k) in seller.supports" :key=k>
                                 <img src="/static/imgs/decrease.png" alt="">
                            <p v-if="seller.supports" >{{seller.supports[k].description}}</p>
                            </div>
                           
                        </div>
                    </div>
                    <!-- 第三部分 -->
                    <div class="part3">
                        <p class="line1"></p>
                        <p class="discout">商家公告</p>                         
                        <p class="line2"></p>
                        <div>
                            <p>{{seller.bulletin}}</p>
                        </div>
                        
                    </div>
                    <!-- 点击关闭 -->
                    <div class="close" @click="hideDetail">
                        <img src="/static/imgs/close01.png" alt="">
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>

import {mapGetters,mapActions} from 'vuex'
import animate from 'animate.css'
export default {
  computed:{
      ...mapGetters([
          'sellerShow',
          'seller'
      ])
  },
  methods:{
      ...mapActions([
          'hideDetail'
      ])
  }
}
</script>
<style lang="less" scoped>
.sellerDetail{
    width: 100%;
    height: 100%;
    background: rgba(7,17,27,0.8);
    position: fixed;
	left: 0;
    top: 0;
    box-sizing: border-box;
    padding: 1.28rem 0.72rem 0.64rem 0.72rem;
   .bg{
        // background: rgba(7,17,27,0.2);
        // width: 100%;
        // height: 100%;
        // filter: blur(0.1rem);
        // position: fixed;
	    // left: 0;
        // top: 0;
        .main{
            // border: 1px solid red;
             height: 5rem;
            /* 第一部分*/
            .part1{
                //  border-bottom: 2px solid rgba(255,255,255,0.2);
                 text-align: center;
                 padding-bottom: 0.56rem;
                 /*店名*/
                 .sellername{
                     font-size: 0.32rem;
                     font-weight: 700;
                     color: #fff;
                     line-height: 0.32rem;
                     margin-bottom: 0.32rem;
                 }
                 /*星星*/
                 .star{
                     height: 0.48rem;
                 }
            }
            /*优惠信息*/
            .part2{
                box-sizing: border-box;
                .discout{
                        color: #fff;
                        font-size: 0.28rem;
                        display: inline-block;
                        margin: -0.01rem 0 0 0.24rem;
                    }
              
                    .line1{
                        float: left;
                        width: 2.24rem;
                        border-bottom: 2px solid rgba(255,255,255,0.2);
                        margin-top:0.1rem;
                    }
                    .line2{
                        float: right;
                        width: 2.24rem;
                        border-bottom: 2px solid rgba(255,255,255,0.2);
                        margin-top:0.1rem;
                    }
                    .discontent{
                        padding: 0.48rem 0 0.56rem 0;
                        // border: 1px solid green;
                        box-sizing: border-box;
                        img{
                            width: 0.32rem;
                        }
                        p{
                            display: inline-block;
                            font-size: 0.24rem;
                            font-weight: 200;
                            color: #fff;
                            line-height: 0.24rem;
                            vertical-align: top;
                            padding:0 0 0.24rem 0.1rem;
                        }
                    }
            }
            .part3{
                /*商家公告*/            
                box-sizing: border-box;
                .discout{
                        color: #fff;
                        font-size: 0.28rem;
                        display: inline-block;
                        margin: -0.01rem 0 0 0.24rem;
                    }
              
                    .line1{
                        float: left;
                        width: 2.24rem;
                        border-bottom: 2px solid rgba(255,255,255,0.2);
                        margin-top:0.1rem;
                    }
                    .line2{
                        float: right;
                        width: 2.24rem;
                        border-bottom: 2px solid rgba(255,255,255,0.2);
                        margin-top:0.1rem;
                    }
                    div{
                        p{
                            font-size: 0.24rem;
                            padding-top: 0.48rem;
                            font-weight: 200;
                            color: #fff;
                            line-height: 0.48rem;
                        }
                    }
            
            }
            .close{              
                text-align: center;
                position: absolute;
                bottom: 1rem;
                left: 48%;      
                           
            }
        }
   
    }
}
</style>


