import axios from 'axios';
import TYPES from '../types.js'

const state = {
    goods:[],
    detailShow : false,
    food : [],
    detailShopCar : false,
    foodSelectRatings : [],
    types1:['推荐','吐槽','全部'],
	selectType1 : 2,
    onlyContent1 : false    
}
const getters = {
    goods(state){
        return state.goods
    },
    detailShow( state ){
		return state.detailShow;
	},
	food( state ){
		return state.food;
    },
    detailShopCar(state){
        return state.detailShopCar
    },
    foodSelectRatings(state){
        return state.foodSelectRatings
    },
	types1( state ){
		return state.types1;
	},
	selectType1( state ){
		return state.selectType1;
	},
	onlyContent1( state ){
		return state.onlyContent1;
    }  

}
const actions = {
        //获取goods数据
        getGoods({commit}){
            // console.log(3)
                axios.get('data.json').then((res)=>{
                    // console.log(res.data.ratings)
                    commit(TYPES.GET_GOODS,res.data.goods)
                })
        },
        showFoodDetail({commit},food){
            commit(TYPES.SHOW_FOODDETAIL,food)
            // console.log(111)
        },
        hideFoodDetail({commit}){
            commit(TYPES.HIDE_FOODDETAIL)
        },
        showMyShopCar({commit}){
            commit(TYPES.SHOW_MYSHOPCAR)
        },
        hideShopCar({commit}){
            commit(TYPES.HIDESHOPCAR)
        },
        //商品详情页 点击筛选评论内容
        foodSelect({commit},{type}){
            commit(TYPES.FOODSELECT,{type})
        },
        // 初始化状态
        chang1Types({commit} ,_types){
            commit( TYPES.CHANGE1_TYPES ,_types);
        },
        // 只看有内容的评价
        toggle1({commit}){
            commit(TYPES.TOGGLE1);
        }


}
const mutations = {
    //获取goods数据
    [TYPES.GET_GOODS](state,data){
        state.goods = data;
        // console.log(state.goods.length)
    },
    [TYPES.SHOW_FOODDETAIL](state,food){
		state.detailShow = true;
        state.food = food;
        // console.log(22)
        // console.log(state.detailShow)
        
    },
    [TYPES.HIDE_FOODDETAIL](state){
        state.detailShow = false;
    },
    [TYPES.SHOW_MYSHOPCAR](state){
        state.detailShopCar = true;
    },
    [TYPES.HIDESHOPCAR](state){
        state.detailShopCar = false;
    },    
    //商品详情页评论筛选
    [TYPES.FOODSELECT](state,{type}){
        state.selectType1 = type;
        state.foodSelectRatings = state.food.ratings.filter( rating =>{
            // 遍历所有评价
            if( state.selectType1 == 2){
                return true;
            }else{
                // 遍历所有点击选中的评价，0 或 1
                return rating.rateType == state.selectType1;
            }
        })
        // console.log(state.foodSelectRatings)
    },
    // 初始化状态
    [TYPES.CHANGE1_TYPES](state,_types){
		state.types1 = _types;
		state.selectType1 = 2;
		//需要遍历的数据			
		state.foodSelectRatings = state.food.ratings;
        state.onlyContent1 = false;
        // console.log(state.foodSelectRatings)
    },
    // 只看有内容的评价    
	[TYPES.TOGGLE1](state){
        state.onlyContent1 = !state.onlyContent1;
        
		state.foodSelectRatings = state.food.ratings.filter( rating =>{
			//   点击只看有内容      评论没有内容
			if( state.onlyContent1 && !rating.text ){
				return false;
			}else if( state.selectType1 === 2 ){
				return true; //查看全部评论
			}else{
				//  
				return rating.rateType == state.selectType1;
			}
		})
    }
}
export default {
    state,
    getters,
    mutations,
    actions
}


