<template>
  <div class="main" id="main">
     <!-- 综合评分 -->
      <div class="comscore">
        <!-- 左侧 -->
          <div class="scoreleft">
                <!-- 综合评分 -->
                <p class="scoretext01">{{seller.score}}</p>
                <p class="scoretext02">综合评分</p>
                <!-- 高于周边商家 -->
                <p class="scoretext03">高于周边商家{{seller.rankRate}}%</p>
          </div>
          <!-- 右侧 -->
          <div class="scoreright">
              
              <!-- 服务态度 -->
                <div class="servicescore">
                    <p class="scoretext04">服务态度</p>
                    <div>
                      <star :score="seller.serviceScore"></star>                                          
                    </div>
                    <p class="scoretext05">{{seller.serviceScore}}</p>
                </div>
                <!-- 服务态度 -->
                <div class="servicescore">
                    <p class="scoretext04">商品评分</p>
                    <div>
                      <star :score="seller.foodScore" ></star>                                              
                    </div>
                    <p class="scoretext05">{{seller.foodScore}}</p>
                </div>
                <!-- 送达时间 -->
                <div class="delivery">
                    <p>送达时间</p>
                    <p class="scoretext06">{{seller.deliveryTime}}分钟</p>
                </div>      
          </div>
      </div>
      <div class="mid"></div>
      <!-- 评论 -->
      <div class="comments">
          <!-- 评论选择组件 -->
          <ratingSelect></ratingSelect>
            <!-- 详细评论内容部分 start-->
            <div class="commentpart2" id="commm">
              <div>
                  <div class="comm_content" v-for="(v,k) in selectRatings" :key='k'>
                    <div class="comm_content_left">
                          <img :src="v.avatar" alt="">
                    </div>
                    <div class="comm_content_right">
                      <div class="comm_user">
                          <p class="username">{{v.username}}</p>
                          <p class="comm_time">{{v.rateTime | moment}}</p>
                      </div>
                      
                      <div class="comm_start">                    
                           <star :score="v.score" class="star-small"></star>  
                          <p>{{v.deliveryTime}}分钟送达</p>                    
                      </div>
                      <p class="user_comm">{{v.text}}</p>
                      <!-- 标签 -->
                      <div class="recommend">
                          <img v-show="v.rateType==0" src="/static/imgs/agree.png" alt="">
                          <img v-show="v.rateType==1" src="/static/imgs/disagree.png" alt="">
                          <p v-for="(v1,k1) in v.recommend" :key='k1'>
                            {{v1}}
                          </p>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
             <!-- 详细评论内容部分 end-->
      </div>
  </div>
</template>
<script>
import star from '../star/star'
import {mapGetters,mapActions} from 'vuex'
import animate from 'animate.css'
import BScroll from 'better-scroll'
import moment from 'moment'
import ratingSelect from '../ratingSelect/ratingSelect'

export default {
  computed:{
      ...mapGetters([
          'ratings',
          'seller',
          'selectRatings'
      ])
  },
  data(){
      return {

      }
  },
  components:{
      star,
      ratingSelect
  },
  filters:{
    moment : function(value, formatString){
       formatString = formatString || 'YYYY-MM-DD HH:mm';
       return moment(value).format(formatString);
    }
  },
		watch:{
			seller(){

			}
		},
		mounted(){
      this.$nextTick( () => {
        if( !this.sellerSCroll ){
          this.sellerSCroll = new BScroll( document.getElementById('commm') ,{
          });
        }else{
          this.sellerSCroll.refresh();
        }
      })
		}
}
</script>
<style lang="less" scoped>
.main{
  width: 100%;
  box-sizing: border-box;
  position: absolute;
	top: 3.6rem;
	bottom: 1rem;
  z-index: -55;
  overflow: hidden;   
  /*padding: 0.36rem 0.48rem 0.36rem 0.48rem;*/
  /*综合评分*/
    .comscore{        
        height: 2rem;
        border-bottom: 2px solid rgba(7, 17, 27, 0.1);
        padding: 0.36rem 0.48rem 0.48rem 0.48rem;
        box-sizing: border-box;
        display: flex;
        .scoreleft{
          height: 1.3rem;
          width: 36.67%;
          border-right: 1px solid rgba(7, 17, 27, 0.1);
          text-align: center;
          .scoretext01{
            font-size: 0.48rem;
            color:rgb(255,153,0);
            line-height: 0.56rem;
            margin-bottom: 0.12rem;
          }
          .scoretext02{
            font-size: 0.24rem;
            color:rgb(7,17,27);
            line-height: 0.24rem;
            margin-bottom: 0.16rem;
          }
          .scoretext03{
            font-size: 0.2rem;
            color:rgb(147,153,159);
            line-height: 0.2rem;
          }
        }
        /*星星*/
        .scoreright{
          box-sizing: border-box;
          padding: 0 0 0 0.48rem;   
          width: 63.33%;
          .servicescore{
            height: 0.5rem;
            display: flex;
            justify-content: space-between;
              .scoretext04{
                  font-size: 0.24rem;
                  color:rgb(7,17,27);
                  line-height: 0.36rem; 
               }
               img{
                 width: 0.32rem;
                 height: 0.32rem;
               }
               .scoretext05{
                 font-size: 0.24rem;
                  color:rgb(255,153,0);
                  line-height: 0.36rem; 
               }
          }
          .delivery{
              font-size: 0.24rem;
              height: 0.5rem;
              display: flex;
          }
          .scoretext06{
              font-size: 0.24rem;
              color:rgb(147,153,159);
              margin-left:0.24rem;
          }
        }    
        
        
    }
    .mid{
      height: 0.36rem;
      background: #f4f5f7;
      
    }
    /* 具体评论*/
    .comments{
      /* border: 1px solid red;
      border-top: 2px solid rgba(7, 17, 27, 0.1);*/
      height: 9rem;
    }

    /*commentpart2----start------*/
    .commentpart2{
      height: 3.5rem;
      box-sizing: border-box;
      padding: 0.36rem;
      overflow: hidden;
      border-top: 2px solid rgba(7, 17, 27, 0.1);
      .comm_content{
        display: flex;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        padding-bottom:  0.36rem;
        box-sizing: border-box;
        margin-bottom: 0.36rem;
        .comm_content_left{
          img{
            width: 0.56rem;
            height: 0.56rem;
            border-radius: 0.28rem;
          }
        }
        /*comm_content_right-----start------*/
        .comm_content_right{
            box-sizing: border-box;
            padding-left: 0.24rem;
              .comm_user{
                  display: flex;
                  .username{
                    font-size: 0.2rem;
                    color: rgb(7,17,27);
                    line-height: 0.24rem;
                  }
                  .comm_time{
                    font-size: 0.2rem;
                    font-weight: 200;
                    color: rgb(147,153,159);
                    line-height: 0.24rem;
                    position: relative;
                    left: 3.2rem;
                  }
              }
            
            .comm_start{
              margin-bottom: 0.16rem;
              img{
                width:0.3rem;
                height: 0.3rem;
              }
              p{
                font-size: 0.2rem;
                font-weight: 200;
                color: rgb(147,153,159);
                line-height: 0.24rem;
                display: inline-block;
                vertical-align: top;
                margin-top: 0.08rem;
              }
            }
            .user_comm{
              font-size: 0.24rem;
              color: rgb(7,17,27);
              line-height: 0.36rem;
            }
            /*推荐菜*/
            .recommend{
              
              img{
                width: 0.5rem;
                height: 0.5rem;
              }
              p{
                display: inline-block;
                width:1.5rem;
                height: 0.36rem;
                text-align: center;
                border: 1px solid rgba(7, 17, 27, 0.1);
                border-radius: 2px;
                font-size: 0.18rem;
                color: rgb(147,153,159);
                line-height: 0.32rem;
                vertical-align: top;
                margin-top: 0.15rem;
                margin-left: 0.15rem;
                box-sizing: border-box;
                padding:0 0.12rem 0 0.12rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }              
           }            
        }
        /*comm_content_right-----end------*/
      }
    }
    /*commentpart2----end------*/
}
</style>


